@import 'settings';

body, html {
  position: relative;
  height: 100%;
  min-height: 100%;
}

.fusion-header{
    position: relative;
    z-index: 100;
}
a.back-to-home {
    float: left;
    background: #dadada;
    color: white;
    text-transform: uppercase;
    font-size: 11px;
    padding: 5px 8px;
    margin-right: 10px;
    -webkit-border-radius: 4px;
    -moz-border-radius: 4px;
    border-radius: 4px;

    &:hover {
        color: white !important;
    }
}
#wrapper .fusion-widget-area .current-menu-item > a{
  color: white;
}
.fusion-main-menu .current-menu-item > a {
  color: white;
  background: $color-orange;
}
.fusion-secondary-header {
    background: $color-header-top !important;
    border-bottom-color: transparent !important;
    position: relative;
    z-index: 200;
}
.logo-slogan {
    color: $color-orange;
    font-weight: 700;
    font-size: 14px;
    margin: 2px 0 0 3px;
}
.fusion-main-menu {
    float: right;
}
.fusion-main-menu > ul > li > a {
    background: $color-secondary;
    padding: 0 10px !important;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;
    text-transform: uppercase;
    font-size: 13px;
    &:hover {
        color: white !important;
        background: $color-orange;
    }
}
.fusion-main-menu > ul > li.current_page_item a{
      color: white !important;
      background: $color-orange;
}

.fusion-page-title-bar {
    background: #fff;
    border-top: none;
    border-bottom: 1px solid #e5e5e5;

    h1{
        line-height: 24px;
    }
}

.fusion-secondary-menu > ul > li > a {
    text-transform: uppercase;
    font-weight: 700;
    font-family: 'Arial';
    line-height: 1;

    &[href='/admin'] {
      background: $color-red;
      font-weight: 400;
      -webkit-border-radius: 3px;
      -moz-border-radius: 3px;
      border-radius: 3px;
    }
}
.fusion-secondary-menu > ul {
   float: right;
}
.fusion-secondary-header {
    .custom-secondary-header-holder {
        position: relative;
        width: $content-width;
        margin: 0 auto;

        .top-sec-menu{
            position: absolute;
            left: 50%;
            margin-top: 6px;
            -webkit-transform: translateX(-50%);
            -moz-transform: translateX(-50%);
            transform: translateX(-50%);
        }

        .sec-header-wrap {
            width: 100%;
            height: 43px;
            display: flex;
            align-content: center;
            align-items: center;
            justify-content: flex-end;
            .item {
                &.csh-socialling{
                    padding-left: 15px;
                }
                &.csh-notify{
                    padding: 0 50px;
                }
            }
        }
    }
}


#notification {
    margin: 0 auto;
    margin-top: 15px;

    li {
        display: inline-block;
        *display: inline;
        zoom: 1;
        margin: 0 5px;

        a {
            position: relative;
            display: block;
            background: $color-secondary;
            color: white;
            width: 42px;
            height: 42px;
            -webkit-border-radius: 50px;
            -moz-border-radius: 50px;
            border-radius: 50px;
            -webkit-transition: all .3s;
            -moz-transition: all .3s;
            transition: all .3s;
            -webkit-box-shadow: -1px 1px 0px rgba(0, 0, 0, 0.5);
            -moz-box-shadow: -1px 1px 0px rgba(0, 0, 0, 0.5);
            box-shadow: -1px 1px 0px rgba(0, 0, 0, 0.5);

            .num {
                top: 0;
                right: -50%;
                transform: translateX(-50%);
                position: absolute;
                background: $color-notify-bubble;
                color: #fff;
                font-size: 10px;
                font-weight: 100;
                width: 22px;
                height: 21px;
                text-align: center;
                padding: 0;
                line-height: 22px;
                vertical-align: middle;
                -webkit-border-radius: 20px;
                -moz-border-radius: 20px;
                border-radius: 30px;
                display: none;

                &.has {
                  display: block;
                }
            }
            &:before {
                content: "";
                padding-top: 15px;
                width: 25px;
                height: 25px;
                margin: 0 auto;
                display: block;
                background-repeat: no-repeat;
                background-position: center;
            }
        }

        &.visited-adv {
            a {
                background: $color-primary;
            }
        }

        a:hover {
            background: $color-orange;
            -webkit-transform: scale(1.1);
            -moz-transform: scale(1.1);
            transform: scale(1.1);
        }

        &.visited-adv a:before {
            background-image: url('images/ico-history.png');
        }
        &.new-adv a:before {
             background-image: url('images/ico-house.png');
        }
    }
}

.header-search, .footer-search{
    margin-top: 27px;
    width: 200px;
    position: relative;
    -webkit-transition: all .3s;
    -moz-transition: all .3s;
    transition: all .3s;

    input[type=text]{
        border-color: #cccfd1;
        color: $color-orange;
        width: 100%;
        line-height: 30px;
        height: 30px;
        padding: 0 8px;
        padding-right: 33px;
    }

    button {
        position: absolute;
        top: 0;
        right: 0;
        line-height: 30px;
        height: 30px;
        padding: 0 10px;
        margin: 0;
        background: transparent;
        border: none;
        cursor: pointer;

        i {
            line-height: 30px;
            padding: 0;
            margin: 0;
            font-size: 18px;
            color: $color-secondary;
        }
    }

    &:hover {
        width: 350px;
    }
}

.header-holder {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  align-items: flex-end;
  margin:0 0 10px 0;

  .fusion-logo {
    float: none;
    flex-basis: 20%;
    margin: 0;
  }
  .side-of-logo {
    float: none;
    flex-basis: 80%;
  }
}

.header-search {
  float: right;
  width: 320px;
  margin-bottom:20px;
}
.footer-search {
  float: right;
}

.search-box {
    background: #fff;

    .sb-inp-wrapper{
        padding: 0 5px;
    }

    .sb-header {
        background: $color-secondary;
        color: white;
        font-size: 26px;
        text-transform: uppercase;
        font-size: 20px;
        padding: 8px 15px;
    }

    .sb-body {
        padding: 15px;
        border: 1px solid #cccfd1;

        label {
            font-size: 13px;
            margin-bottom: 4px;
            display: block;
            color: #878787;
        }

        input[type=number],
        select {
            width: 100%;
            height:40px;
            line-height: 40px;
            padding: 0 10px;
            font-size: 16px;
            border: 1px solid #ccc;
            color: $color-orange;
        }
    }

    .sb-footer{
        background: #f2f2f2;
        border: 1px solid #cccfd1;
        border-top: none;
        padding: 15px 15px;

        a {
            font-size: 13px;
            color: $color-secondary;
        }
    }
}

.footer-after-menu {
    .social-links, .footer-search {
        display: inline-block;
        *display: inline;
        zoom:1;
        float: right;
        height: 30px;
    }
    .footer-search{
        margin: 0;
        width: 250px;

        input[type=text] {
            height:30px;
            line-height:30px;
        }
    }

    .social-links{
        margin: 0 25px;
        .fusion-social-networks-wrapper{
            padding: 0 5px;
        }
        a {
            margin: 0 !important;
            line-height: 30px;
            color: white !important;
        }
    }
}

.fusion-widget-menu{
    li{
        a{
            text-transform: uppercase;
            text-align:center !important;
        }
    }
}
.fusion-footer-widget-column{
    margin-bottom: 5px;
}
.fusion-copyright-content{
    ul.menu {
        margin: 0 auto;
        padding: 0;

        li {
            display: inline-block;
            *display: inline;
            zoom: 1;
            vertical-align: middle;

            a{
                font-size: 12px;
                padding: 5px;
                color: white;
                display: block;
                line-height: 15px;
                -webkit-transition: all .3s;
                -moz-transition: all .3s;
                transition: all .3s;
                text-shadow: 0px 1px 2px rgba(0, 0, 0, 0.5);
            }

            a:hover {
                font-size: 13px;
            }
        }
    }
}
.footer-right-side {
    margin-top: 15px;
}

.listing-sc{
  h1 {
    margin: 0 0 15px 0;
  }

  .header {
    .morev{
      float: right;
      padding: 10px;

      a{
        color: $color-secondary;
      }
    }
    h2 {
      text-align: center;
      font-size: 2.8em !important;
      text-transform: uppercase;
      color: $color-secondary;
    }
  }

  .listing-list-set-viewed,
  .listing-list-set-recomended{
    .header {
      h2 {
        color: #79828e !important;
      }
    }
  }
}

.fusion-page-title-bar h1{
  color: $color-secondary;
}

.page-title-shares{
  float: right;
  & > div {
    display: inline;
    *display: inline;
    zoom: 1;
    vertical-align: middle;
    float: left;

    & > div {
      display: inline;
      *display: inline;
      zoom: 1;
      color: white;
      vertical-align: middle;
      line-height: 30px;
      text-align: center;
      float: left;
      width: 30px;
      height: 30px;
      border-radius: 50px;
      margin-left: 3px;

      &.default {
        background-color: $color-primary;
      }
      &.fb {
        background-color: #3B5998;
      }
      &.gplus {
        background-color: #DB4437;
      }
      a {
        display: block;
      }
      a, a i {
        color: white;
      }
    }
    &.socials {
      margin-right: 10px;
    }
  }
}

body.ingatlan_page{
  .divider {
    height: 1px;
    width: 100%;
    margin: 10px 0;
    background: #d7d7d7;
  }
  #main {
    background: #ffffff;
    padding: 0 !important;

    .fusion-row {
      width: 100% !important;
      max-width: initial !important;

      .page-holder{
        max-width: 1200px;
        margin: 0 auto;
      }
    }

    .data-main-holder{
      background: #e9eff2;
      padding: 20px 0;
      border: 1px solid #dedede;
      border-left: none;
      border-right: none;
    }
  }
}

.fusion-modal h1, .fusion-post-content h1, .fusion-widget-area h1, .post-content h1{
  color: $color-orange;
  text-transform: uppercase;
  font-weight: bold;
}

.show-on-mobile{
  display: none;
}

.megtekintett-page-view,
.news-page-view{
  width: $content-width;
  margin: 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 25px 0;

  .search-side{
    flex-basis: 30.6666%;
    margin-right: 4%;
  }
  .listing-content{
    flex-basis: 65.3333%;
    position: relative;

    .set-watced-prop{
      position: absolute;
      right: 0;
      font-size: 0.8em;
      background: white;
      padding: 5px;
      line-height: 1;
      border-radius: 4px;
    }

    h1 {
      font-family: 'Roboto';
      font-weight: 500;
      line-height: 1.4;
      letter-spacing: 0px;
      color: $color-orange;
      text-transform: uppercase;
      font-size: 34px;
      margin: 0 0 15px 0;
      font-weight: bold;
    }
  }
}

body.megtekintett_template_page,
body.news_template_page{
  #content {
    background: #eaf0f3;
  }
}


.ingatlan-page-view {
  .history-list,
  .recomended-list{
    padding: 20px 0;

    .header {
      padding: 0 0 15px 0;

      h2 {
        font-size: 2.2em !important;
      }
    }
  }
  .fusion-tabs {
    margin: 0 !important;
    .nav {
      background: #cbcbcb;
      margin-bottom: -1px;

      .nav-tabs{
        background: transparent !important;
        border: none !important;

        li .tab-link {
          background: transparent !important;
          border: none !important;
          border-left: 1px solid #ffffff !important;
          color: white !important;
          text-transform: uppercase;

          h4 {
            color: white !important;
            font-size: 1.2em !important;
          }
        }
        li.active + li .tab-link,
        li:first-child .tab-link {
          border-left: none !important;
        }
        li.active .tab-link {
          background: #ffffff !important;
          border: 1px solid $color-primary !important;
          border-bottom: 1px solid #ffffff !important;
          h4 {
            color: $color-primary !important;
          }
        }
      }
    }

    .tab-content{
      margin-top: -2px;
      .tab-pane {
        padding: 0 !important;
        background: #ffffff;
        border: 1px solid $color-primary !important;
        .pad {
          padding: 15px !important;
        }
      }
    }
  }

  .data-top,
  .data-main {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;

    & > .data-top-left {
      flex-basis: 60%;

      .cwrapper{
        padding-right: 10px;
        position: relative;
        .images{
          background: #f2f2f2;
          .profil {
            position: relative;
            height: 520px;
            overflow: hidden;

            img{
              position: absolute;
              left: 50%;
              top: 50%;
              width: 110%;
              max-width: 110%;
              -webkit-transform: translate(-50%, -50%);
              -moz-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
            }
          }
          .stack {
            .stack-wrapper{
              .items {
                width: 100%;
                table-layout: fixed;
                display: table;


                .i {
                  border: 5px solid #efefef;
                  position: relative;

                  img {
                    position: absolute;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);
                    width: auto;
                    max-width: initial;
                    height: 115%;
                    max-height: 115%;
                  }

                  &.orientation-portrait {
                    img {
                      height: auto;
                      max-height: initial;
                      max-width: 115%;
                      width: 115%;
                    }
                  }
                }
              }
            }
          }
        }
      }
    }
    & > .data-top-right {
      flex-basis: 40%;
      .properties {
        margin-top: 10px;
      }
      .title{
        margin: 0;
        position:relative;
        h1 {
          margin: 0 0 10px 0;
          font-size: 2em;
          line-height: 1;
          color: #78818d;
          font-family: 'Arial';
          font-weight: bold;
          letter-spacing: -1px;
        }

        .subtitle {
          font-size: 1.3em;
          line-height: 1.3;
          margin: 0 0 20px 0;

          .addr{
            font-family: 'Arial';
            font-weight: bold !important;
            color: $color-secondary;
          }
        }

        .icons {
          position: absolute;
          right: 0;
          top: 0;
          & > div {
            display: inline-block;
            *display: inline;
            zoom: 1;
            margin-left: 5px;

            a {
              display: block;
              background: #bbb;
              line-height: 52px;
              height: 52px;
              width: 52px;
              text-align: center;
              color: #757575;
              font-size: 22px;

              i {
                margin: 0;
                padding: 0;
              }
            }
            &.facebook {
              a{
                background: #0061ad;
                color: white;
                &:hover{
                  background: darken(#0061ad, 10%);
                }
              }
            }
            &.gplus {
              a{
                background: #bf2121;
                color: white;
                &:hover{
                  background: darken(#bf2121, 10%);
                }
              }
            }
          }
        }
      }
    }

    & > .data-main-left {
      flex-basis: 65%;
    }
    & > .data-main-right {
      flex-basis: 35%;
    }
  }

  .parameters {
    .line {
      border-bottom: 1px solid #cacecf;

      .param {
        border-right: 1px solid #dedede;
        display: inline-block;
        width: 50%;
        margin: 0 -1px;

        .text, .value {
          display: inline-block;
          margin: 0 -1px;
          line-height: 0;
          padding: 18px 0;
          font-size: 1.15em;
          vertical-align: middle;
          max-height: 35px;
        }
        .text {
          width: 50%;
          padding: 0 0 0 15px;
        }
        .value {
          width: 50%;
          font-weight: bold;
          font-family: 'Arial';
          color: #444444;

          i {
            line-height: 0;
            padding: 0 2px 0 0;
            margin: 0;
            color: $color-primary;

          }

          .na {
            color: #999999;
            font-weight: normal;
            font-style: italic;
          }
        }
        &:nth-child(even) {
          border-right: none;
        }
      }

      &:nth-child(odd) {
        background: #f2f2f2;
      }
      &:last-child{
        border-bottom: none;
      }
    }
  }

  .data-top {
    padding: 20px 0;
  }

  .data-main{
    margin: 25px 0 20px 0;
  }

  .properties{
    .list{
      border: 1px solid #d7d7d7;

      display: flex;
      flex-wrap: wrap;

      .e{
        border-bottom: 1px solid #e0dede;
        border-right: 1px solid #e0dede;
        padding: 11px 0;
        font-size: 0.94em;
        flex-basis: calc(100% / 3);

        .h,
        .v {
          text-align: center;
          .na {
            font-style: italic;
            font-weight: normal;
            color: #949494;
            font-size: 0.9em;
            letter-spacing: -0.4px;
          }
        }

        &.price {
          border-right: none;
          flex-basis: 66.6666%;
          background: $color-secondary;
          text-align: right;
          color: white;
          font-weight: bold;
          font-family: 'Arial';
          padding: 8px;
          line-height: 65px;
          .current-price {
            font-size: 2.8em;
            line-height: 88px;

            .pt {
              font-size: 0.5em;
              font-weight: normal;
              letter-spacing: -1px;
            }
          }
          .old-price {
            position: relative;
            font-size: 1.3em;
            float: left;
            font-weight: normal;
            line-height: 88px;
            color: #9ec8dc;
            &:before {
              position: absolute;
              width: 100%;
              height: 2px;
              background: #d9ecf5;
              left: 0;
              top: 50%;
              content: "";
              -webkit-transform: rotate(-10deg);
              -moz-transform: rotate(-10deg);
              transform: rotate(-10deg);
              content: "";
            }
          }
        }
        &:nth-child(3n+3) {
          border-right: none;
        }
        &.smaller {
          .v {
            font-size: 1.3em;
          }
        }

        &.e2x{
          flex-basis: 66.6666667%;
        }

        &.toleft{
          padding: 11px;
          .ico,
          .h,
          .v{
            text-align: left;
          }
        }
      }

      .h{
        color: #c8c8c8;
        font-size: 1.1em;

        .ico {
          text-align: center;

          img{
            width: 20px;
            height: 20px;
          }
        }
      }

      .v{
        font-weight: bold;
        font-size: 2em;
        font-family: 'Arial';
        color: #8f8e8e;

        .na {
          font-size: 0.55em !important;
          color: #9c9c9c !important;
        }
      }

    }

  }

  .contact{
    background: #EFEFEF;
    padding: 15px;
    text-align: left;
    border: 1px solid #C1C4C6;
    border-top: none;
    line-height: 1.6;
    color: #767676;

    .profil-img {
      float: left;
      padding: 0 8px 8px 0;
    }

    i {
      margin-right: 8px;
      color: #8c8c8c;
    }

    .title {
      color: $color-secondary;
      margin-bottom: 8px;
      font-size: 1.5em;
    }
    .name {
      font-size: 1.2em;
      color: black;
    }
    .phone{
      font-size: 1.45em;
      color: #444444;
      font-weight: bold;
      font-family: 'Arial';

      i{color: $color-secondary;}
    }
    .email{
      a, a:link, a:visited {
        font-size: 0.9em;
        color: $color-secondary !important;
      }
    }

    .mail {
      text-align: right;
      margin-top: 4px;
      a {
        font-size: 0.9em;
        background: $color-red;
        color: white;
        text-decoration: none;
        padding: 5px 8px;
        line-height: 1;
        -webkit-border-radius: 3px;
        -moz-border-radius: 3px;
        border-radius: 3px;
        text-transform: uppercase;
        i {
          color: white;
        }
      }
    }
  }

  .description-block{
    border:1px solid #e0dede;
    margin-right: 5px;
    margin-bottom: 12px;

    .head{
      border-bottom: 1px solid #e0dede;
      background: #F9F9F9;
      line-height: 50px;
      color: $color-primary;
      font-size: 1.2em;

      .ico {
        float: left;
        border-right: 1px solid #e0dede;
        text-align: center;
        width: 50px;
        margin-right: 15px;

        i{
          font-size: 24px;
          margin: 0;
          padding: 0;
          vertical-align: middle;
        }
      }
    }

    .text {
      padding: 15px;
      font-size: 0.9em;
      color: black;

      strong{
        font-family: 'Arial';
        font-weight: bold;
      }

      &.doc-list {
        padding: 8px;
        .doc a {
          display: block;
          border-bottom: 1px solid #f1f1f1;
          padding: 8px;
          line-height: 1;
          color: #747474;

          i {
            margin-right: 5px;
          }

          .doc-info {
            float: right;
            color: #bdbdbd;
            font-size: 0.9em;
          }

          &:hover {
            background: #f1f1f1;
          }
        }
        .doc:last-child a{
          border-bottom: none;
        }
      }
    }
  }

  .map-block{
    border: 1px solid #e0dede;
    min-height: 200px;
    margin-left: 5px;
    background: #ffffff;

