.listing-searcher-holder {
  background: #ffffff;
  margin: 0 auto;
  position: relative;
  z-index: 100;
  outline: 4px solid rgba(0, 0, 0, 0.13);

  /* Main home style v1 */
  &.style-v1 {
    width: 80%;
    margin-top: -71px;
    &:after {
      display: block;
      position: absolute;
      width: 100%;
      height: 10px;
      content: "";
      bottom: -10px;
      left: 50%;
      z-index: 10;
      background-image: url("../../images/searcher-shadow.png");
      background-repeat: no-repeat;
      background-position: top center;
      background-size: 100%;
      -webkit-transform:translateX(-50%);
      -moz-transform:translateX(-50%);
      transform:translateX(-50%);
    }
  }
  /* Listing style v2 */
  &.style-v2 {
    width: 100%;
    margin-top: 10px;

    .submit {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      flex-wrap: wrap;
      align-items: stretch;
      justify-content:center;

      button {
        flex-basis: 50%;
        font-size: 0.89em;
        background: $color-secondary;

        i{
          font-size: 14px;
          margin-right: 4px;
        }

        &:hover {
          background: $color-primary;
        }
      }

      .backlist{
        flex-basis: 50%;
        background: lighten(desaturate($color-secondary, 20), 15%);
        color: black;
        text-align: center;

        a{
          display: block;
          line-height: 50px;
          font-size: 0.9em;
          color: white;
        }
      }
    }
  }
  /* Ingatlan style floating */
  &.style-floating {
    width: 320px;
    position: fixed;
    left: 0;
    top: 80px;
    z-index: 99999;
  }

  .searcher-header{
    position: relative;;
    left: 0;
    width: 100%;
    top: 0;
    background: $color-secondary;
    color: white;

    ul {
      margin: 0;
      padding: 0;
      li {
        display:inline-block;
        *display: inline;
        zoom: 1;
        vertical-align: middle;
        margin-right: 5px;

        &.title{
          text-transform: uppercase;
          font-size: 1.4em;
          padding: 0 15px;
        }

        input[type=radio] {
          display: none;
        }

        label {
          cursor: pointer;
          position: relative;
          display: block;
          height: 50px;
          line-height: 50px;
          background: $color-secondary;
          color: white;
          padding: 0 15px 0 35px;
          text-transform: uppercase;
          font-weight: bold;
          font-family: 'Arial';
          font-size: 1.2em;
          letter-spacing: 1px;

          &:before {
            font-family: 'FontAwesome';
            content:"\f111";
            position: absolute;
            left: 12px;
            font-weight: normal;
            color: #c7c7c7;
            width: 20px;
            text-align: center;
          }
        }

        input[type=radio]:checked + label {
          background: $color-primary;
          &:before {
            content:"\f192";
            color: white;
          }
        }

        .ico {
          background: darken($color-secondary, 5%);
          height: 50px;
          text-align: center;
          padding: 0 15px;

          .mobile-text{
            color: white;
            font-size: 1.3em;
            padding-left: 8px;
            text-transform: uppercase;
            font-family: 'Arial';
            font-weight: bold;
            visibility: hidden;
            position: absolute;
          }

          i{
            color: white;
            padding: 0;
            margin: 0;
            height: 50px;
            font-size: 24px;
            line-height: 50px;
          }
        }

        &:first-child{
          margin-right: 0;
        }
      }
    }
  }
  .searcher-wrapper {
    padding: 5px;

    .form-items {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      width: 100%;
      flex-wrap: wrap;
      align-items: flex-end;

      .inp {
        padding: 8px;
        position: relative;

        label {
          line-height: 1;
          color: #878787;
          margin-bottom: 10px;
          display: block;
        }

        &.inp-submit{
          text-align: right;
          button{
            color: white;
            background: darken($color-primary, 0%);
            line-height: 36px;
            padding: 0 15px;
            height: 36px;
            width: 100%;
            font-family: 'Arial';
            text-transform: uppercase;
            font-weight: bold;
            border: none;
            font-size: 1em;
            position: relative;
            text-align: right;

            i{
              background: $color-primary;
              position: absolute;
              left: 0;
              line-height: 24px;
              text-align: center;
              color: #fff;
              padding: 0 5px;
              margin: 6px 0 0 5px;
            }

            &:hover {
              background: $color-primary;
              cursor: pointer;
            }
          }
        }

        &.inp-city{
          flex-basis: 40%
        }
        &.inp-kategoria{
          flex-basis: 30%;
        }
        &.inp-allapot{
          flex-basis: 30%;
        }
        &.inp-status{
          flex-basis: 30%;
        }

        &.inp-rooms{
          flex-basis: 20%;
        }
        &.inp-alapterulet{
          flex-basis: 15%;
        }

        &.inp-price-min{
          flex-basis: 25%;
        }
        &.inp-price-max{
          flex-basis: 25%;
        }
        &.inp-submit{
          flex-basis: 15%;
          button {
            float: right;
          }
        }
      }

      input[type=text],
      input[type=number],
      select {
        width: 100%;
        height: 35px;
        line-height: 35px;
        padding: 0 8px;
        border: 1px solid #cccccc;
        font-size: 1.2em;
        color: $color-orange;
      }
    }
  }
  .searcher-footer {
    padding: 15px;
    background: #f2f2f2;
    border-top: 1px solid #cccfd1;
    box-shadow: inset 0 -1px 0 #fff;
    line-height: 1;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;

    .option-holder{
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      width: 100%;
    }

    .options-more{
      a{
        font-size: 0.8em;
      }
      flex-basis: 20%;
    }

    .options-selects{
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      flex-wrap: wrap;
      flex-basis: 80%;

      & > div {
        padding: 0 15px 8px 0;
        input[type=checkbox] + label{
          padding-left: 20px;
          font-size: 0.9em;
          color: #808080;
          line-height: 24px;

          &:before {
            color: #9d9d9d;
            height: 20px;
            line-height: 20px;
            font-size: 20px;
            vertical-align: middle;
            margin-top: 2px;
          }
        }
        input[type=checkbox]:checked + label{
          &:before {
            color: #6cbf92;
          }
        }
        &.secondary-param {
          display: none;

          &.show {
            display: block;
          }
        }
      }
    }
  }

  /* MOD v2 */
  &.style-v2,
  &.style-floating,
  &.style-map {
    .searcher-header {
      left: -1px;
      right: -1px;
      font-size: 0.9em;
      ul {
        width: 100%;
        background: #eaeaea;
        li {
          padding: 8px 15px;
          color: white;
          text-transform: uppercase;
          font-family: 'Arial';
          font-weight: bold;
          width: 50%;
          margin: 0;

          .head-title {
            display: block;
            font-size: 1.15em;
            color: $color-secondary;
            line-height: 30px;
          }

          .selecting{
            select {
              line-height: 30px;
              height: 30px;
              display: block;
              width: 100%;
            }
          }
        }
      }
    }

    .searcher-footer{
      .option-holder {
        display: block;
      }
      .options-selects {
        margin-top: 10px;
        & > div {
          flex-basis: 50%;
        }
      }
    }

    .searcher-wrapper{
      .form-items{
        .inp{
          &.inp-kategoria,
          &.inp-status,
          &.inp-allapot,
          &.inp-alapterulet,
          &.inp-rooms,
          &.inp-price-min,
          &.inp-price-max{
            flex-basis: 50%;
          }

          &.inp-city {
            flex-basis: 100%;
          }
        }
      }
    }
    .submit button {
      display: block;
      width: 100%;
      text-align: center;
      background: $color-secondary;
      color: white;
      border: none;
      padding: 15px;
      line-height: 20px;
      cursor: pointer;
      font-size: 1.2em;
      font-family: 'Arial';
      font-weight: bold;
      text-transform: uppercase;

      i {
        font-size: 20px;
        line-height: 20px;
        margin-right: 10px;
        padding: 0;
      }

      &:hover {
        background: $color-primary;
      }
    }
  }
  &.style-floating {
    left: -320px;
    .searcher-wrapper{
      padding: 5px;
      .vlabel {
        cursor: pointer;
        background: #B1241C;
        color: white;
        text-transform: uppercase;
        position: absolute;
        -webkit-transform: rotate(-90deg);
        -moz-transform: rotate(-90deg);
        transform: rotate(-90deg);
        right: -225px;
        top: 128px;
        line-height: 1;
        font-size: 1.1em;
        padding: 15px 80px;
      }
    }

    &.opened{
      left: 0;
    }
  }

  &.style-map {
    outline: none;
    background: #fafafa;
    border: 3px solid #d2d2d2;

    .searcher-header{
     left: 0;
     right: 0;
    }
    .searcher-header ul{
      background: #eaeaea;
      li, li .head-title{
        width: 100%;
        float: none;
        color: $color-secondary;
        text-align: center;
        padding: 0;
      }
    }
    li .head-title{
      padding: 10px;
      font-size: 1.3em;
    }

    .head-title {
      padding: 10px 0 !important;
    }

    .submit {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      flex-wrap: wrap;
      align-items: stretch;
      justify-content:center;

      button {
        flex-basis: 50%;
        font-size: 0.89em;
        background: $color-secondary;

        i{
          font-size: 14px;
          margin-right: 4px;
        }

        &:hover {
          background: $color-primary;
        }
      }

      .backlist{
        flex-basis: 50%;
        background: lighten(desaturate($color-secondary, 20), 15%);
        color: black;
        text-align: center;

        a{
          display: block;
          line-height: 50px;
          font-size: 0.9em;
          color: white;
        }
      }
    }

    .form-items{
      .inp {
        display: none;
        padding:4px;

        input, select{
          font-size: 0.9em;
        }

        label {
          font-size: 0.8em;
        }

        &.inp-city,
        &.inp-toggler,
        &.showed {
          display: block;
        }

        &.inp-toggler{
          flex-basis: 100%;
          text-align: center;

          a{
            font-size: 0.84em;
            color: #666666;
          }
        }
      }
    }
  }
}

