        }

        &:hover {
          .excerpt {
            bottom: 0%;
          }
          .photo {
            opacity: 0.1;
            filter: alpha(opacity=10);
          }
        }

        .important-options{
          border-top: 1px solid #cccccc;
          display: flex;
          flex-wrap: wrap;
          width: 100%;
          background: #ffffff;

          & > .opt {
            flex-basis: 50%;
            font-size: 1em;
            padding: 5px;
            line-height: 18px;
            .d {
              padding: 0;
              line-height: 10px;
              font-size: 0.9em;
              color: #79828e;
              text-align: center;
              .ico {
                height: 18px;
                width: 25px;
                margin-right: 5px;
                text-align: center;
                float: left;
                img {
                  height: 100%;
                }
              }

              .t {
                line-height: 18px;
                float: left;
              }
              .v {
                line-height: 18px;
                float: right;
                padding: 0 3px;

                &.c {
                  background: #f1f3f6;
                  padding: 5px;
                  font-size: 0.8em;
                  line-height: 1;
                  letter-spacing: -1px;
                  text-align: center;
                  -moz-border-radius: 15px;
                  -webkit-border-radius: 15px;
                  border-radius: 15px;
                  width: 20px;
                }
              }
            }
          }

          & > .opt:last-child{
            border-right: none;
          }
        }
      }
    }
  }

  &.style-standard,
  &.style-slide {
    .prop-item-wrapper {
      border: 1px solid #d9d9d9;

      .dropoff {
        right: 5px !important;
        top: 17px !important;
      }

      .image{
        .excerpt {
          background: transparentize($color-primary, 0.5);
          padding: 10px;
          font-size: 0.8em;
          color: white;
        }
      }

      .prim-line{
        background: white;
        color: #f5f5f5;
        text-align: center;
        padding: 15px 0;

        a {
          color: $color-secondary;
          font-size: 1.1em;
          text-align: center;
          font-weight: bold;
          font-family: 'Arial';

          img {
            height: 16px;
            vertical-align: middle;
          }
        }
      }
      .sec-line{
        background: desaturate($color-secondary, 10);
        color: #f5f5f5;

        & > .linkto{
          a {
            background: darken($color-secondary, 10%);
            color: #f5f5f5;
          }
        }
      }
    }
  }

  &.style-simple-priced {
    .prop-item-wrapper {
      border: 1px solid #d9d9d9;
      .image{
        height: 140px !important;
        .excerpt {
          background: rgba(214, 44, 48, 0.8);
          padding: 10px;
          font-size: 0.8em;
          color: white;
        }
      }
      .sec-line{
        background: #79828e;
        color: #f5f5f5;

        .price {
          flex-basis: 100% !important;
          text-align: center;
        }
      }
    }
  }

  &.style-maplist {
    .prop-item {
      flex-basis: 100%;
    }
  }

  &.im1{
    .prop-wrapper{
      .prop-item{
          width: calc(100% / 1);
      }
    }
  }

  &.im3{
    .prop-wrapper{
      .prop-item{
          width: calc(100% / 3);
          font-size: 0.9em;

          .image {
            height: 180px;
          }
      }
    }
  }

  &.im4{
    .prop-wrapper{
      .prop-item{
          width: calc(100% / 4);
      }
    }
  }
  &.im5{
    .prop-wrapper{
      .prop-item{
          width: calc(100% / 5);
      }
    }
  }
}


.listing-list-set-unwatched,
.listing-list-set-viewed,
.listing-list-set-get{
  h1 {
    margin: 0 0 5px 0 !important;
  }
  .subtitle {
    color: black;
    font-size: 1.2em;
    margin: 0 0 15px 0;
  }
  .listing-nodata{
    text-align: center;
    font-size: 1.3em;
    margin-top: 50px;
    color: #7ea0b1;
    display: block;
    width: 100%;

    i {
      font-size: 72px;
      color: #366c86;
    }

    h2 {
      color:  #286380;
      font-weight: bold;
      font-size: 2em;
      margin: 0 0 8px 0;
    }
  }
}

.map-page-view{
  min-height: 1000px;
  height: 1000px;
  background-color: #afafaf;
  position: relative;
  display: flex;
  flex-wrap: nowrap;


  .map-view {
    position: relative;
    height: 100%;
    min-height: 100%;
    flex-basis: 70%;
  }

  .item-loader{
      position: fixed;
      left: 0;
      top: 0;
      right: 0;
      bottom: 0;
      z-index: 999;
      background: transparentize($color-secondary, 0.05);
      color: white;

      .text{
        position: absolute;
        top: 50%;
        left: 50%;
        font-size: 1.3em;
        transform: translate(-50%, -50%);
      }
  }

  .data-list {
    position: relative;
    background: white;
    height: 100%;
    flex-basis: 30%;
    -webkit-box-shadow: 4px 0 4px rgba(9, 67, 95, 0.28);
    -moz-box-shadow: 4px 0 4px rgba(9, 67, 95, 0.28);
    box-shadow: 4px 0 4px rgba(9, 67, 95, 0.28);
    z-index: 999;
    overflow: hidden;

    .listing{
      height: 100%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      position: absolute;
    }

    .listing-items {
      height: 100%;
      position: relative;
      overflow: auto;
      padding-bottom: 45px;

      #load-more-page{
        text-align: center;
        font-size: 0.8em;
        background: #f1f1f1;
        padding: 8px;
        text-transform: uppercase;
        color: black;
        cursor: pointer;
        font-weight: bold;
        font-family: 'Arial';

        &:hover{
          background: $color-secondary;
          color: white;
        }
      }

      .page-info{
        text-align: center;
        padding: 8px;
        font-size: 0.7em;
        background: #f1f1f1;
        color: black;
        text-transform: uppercase;
      }

      .prop-item{
        display: flex;
        flex-wrap: wrap;
        border: 2px solid transparent;
        border-bottom: 2px solid #f1f1f1;
        cursor: pointer;

        .img{
          flex-basis: 25%;

          img{
            width: 100%;
            max-width: 100%;
          }
        }

        .dt {
          flex-basis: 75%;
          padding: 5px;
          font-size: 1.1em;

          .hided {
            display: none;
          }
          .hided:not(.hasdata){
            padding: 0;
            margin: 0;
            height: 1px !important;
          }

          .title{
            a{
              font-size: 1.2em;
              font-weight: bold;
              color: $color-secondary;
            }
          }

          .info {
            font-size: 0.9em;
            margin: 4px 0;

            .pos {
              color: $color-orange;
            }

            .price {
              font-size: 0.9em;
              color: $color-red;
              margin-right: 5px;
            }
          }
          .label{
            color: white;
            font-size: 0.7em;
            padding: 0;
            margin-top: 4px;

            span {
              padding: 2px 4px;
            }
          }
          .desc{
            font-size: 0.7em;
            color: #888888;
            height: 30px;
            overflow: hidden;
          }
        }


        &:hover{
          background: #f3f3f3;
          border: 2px solid #eaeaea;
          -webkit-box-shadow: 0 5px 30px -10px #000000;
          -moz-box-shadow: 0 5px 30px -10px #000000;
          box-shadow: 0 5px 30px -10px #000000;
          .hided {
            display: block;
          }
        }

        &.focused{
          background: $color-orange;
          color: white;

          .hided{
            display: block !important;
          }

          .title a, .price, .pos, .desc {
            color: white !important;
          }
        }
      }
    }

    .listing-header {
      background: $color-orange;
      text-align: center;
      color: white;
      font-weight: bold;
      font-size: 1.2em;
      padding: 10px;
    }
  }
}

.map-box-content{
  padding: 15px;
  background: rgba(255, 255, 255, 0.9);
  border: 5px solid #d2d2d2;
  h2{
    font-family: 'Arial' !important;
    color: $color-secondary;
    font-weight: bold;
    text-transform: uppercase;
    margin: 0;
    line-height: 1.2;
    font-weight: bold !important;
  }

  .title {
    padding: 10px 0;
  }

  .pos{
