    }

  }
}

.listing-list-set-viewed,
.listing-list-set-get{
  h1 {
    margin: 0 0 15px 0 !important;
  }
}
.pagination{
  float:none;
  text-align: center;
  .page-numbers{
    line-height: 1;
    padding: 4px 8px;
    background: #ffffff;
    text-align: center;
    color: black;
    border: 1px solid #d9d9d9;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    border-radius: 3px;

    &:hover {
      background: $color-secondary;
      color: white;
    }

    &.current {
      background: $color-orange;
      border: 1px solid darken($color-orange, 10%);
      color: white;
      margin: 0;
    }
  }
}
.listing-list-holder
{
   .slick-initialized .slick-slide{
      height: auto !important;
   }
   .slick-next, .slick-prev{
     &:before {
       font-size: 20px;
     }
    background: transparentize(black, 0.81);
    height: 30px;
    width: 30px;
   }

  .slick-next{
    right: -34px;
  }

  .slick-prev{
    left: -34px;
  }
  .slick-dots{
    bottom: -36px;
    li{
      margin: 0;
    }
    li button{
      padding: 2px;
    }
    li button:before{
      color: transparentize(black, 0.4);
    }
    li.slick-active button:before{
      color: transparentize($color-secondary, 0.2);
    }
  }
}

